.visual {
	width: 100%;
	height: calc(100vh - 100px);
	position: relative;
	overflow: hidden;
	transition: 1s;
	.imgBox {
		width: calc(100% - 340px);
		height: 100%;
		background: url(../img/visual1.jpg) no-repeat center/cover;
		position: absolute;
		left: 0;
		z-index: 1;
		//filter: grayscale(100%);
		transition: 1s;
		animation: visualImg 1.5s ease-in-out;
	}
	.bgBox {
		width: 340px;
		height: 100%;
		background: #00df70;
		position: absolute;
		right: 0;
		transition: 1s;
		animation: visualBg 1.5s ease-in-out;
	}

	@keyframes visualImg {
		0% {
			left: -100%;
		}
		50%{
			left: 0;
		}
		70%{
			left: -5%;
		}
		80%{
			left: 0;
		}
		90% {
			left: -3%;
		}
		100% {
			left: 0;
		}
	}
	@keyframes visualBg {
		0% {
			right: -340px;
		}
		50% {
			right: 0px;
		}
		70% {
			right: -5%;
		}
		80% {
			right: 0px;
		}
		90% {
			right: -3%;
		}
		100% {
			right: 0px;
		}
	}
