	.floatingBox {
		@include floatingBox();
		top: 50%;
		left: calc(100% - 540px);
		transform: translateX(-10%) translateY(-50%);
		width: 500px;
		height: 280px;
		animation: visualBox 0.9s linear 1s;
		transform-origin: 50%;
		.btnMore {
			@include btnMore();
			position: absolute;
			bottom: -30px;
			left: 50%;
			transform: translateX(-50%);
		}
		.txtBox {
			position: absolute;
			top: 0;
			right: 0;
			width: 400px;
			height: 100%;
			background: #fffffe;
			.txt {
				width: 80%;
				position: absolute;
				top: 45%;
				left: 50%;
				transform: translateY(-50%) translateX(-50%);
				h1 {
					@include txtBg();
				}
				p {
					font: 14px/1.4 "Open Sans";
					color: #ccc;
					margin-top: 30px;
				}
			}
		}
	}
	.sns {
		position: absolute;
		bottom: 5%;
		right: 5%;
		ul {
			width: 120px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			li {
				a {
					color: #fffffe;
					font-size: 20px;
				}
			}
		}
	}
}

