.location {
	> .inner {
		width: 1180px;
		margin: 0 auto;
		text-align: center;
		h1 {
			font: bold 26px/1 "Open Sans";
			color: #6b6b6b;
			margin-bottom: 20px;
		}
		h2 {
			font: 14px/1.7 "Open Sans";
			color: #6b6b6b;
			margin-bottom: 40px;
		}
		h3 {
			font: 16px/1.4 "Open Sans";
			color: #6b6b6b;
			letter-spacing: -1px;
			margin-bottom: 20px;
		}
		h4 {
			font: 16px/1.4 "Open Sans";
			color: #6b6b6b;
			margin-bottom: 10px;
		}
		p {
			font: 12px/1.7 "Open Sans";
			color: #6b6b6b;
		}
		span {
			font: 10px/1.7 "Open Sans";
			color: #6b6b6b;
		}

		.contactBox {
			display: flex;
			align-items: top;
			justify-content: space-between;
			margin-bottom: 60px;
			.mapArea {
				width: 50%;
				&::after {
					clear: both;
					content: "";
					display: block;
				}
				#map {
					width: 100%;
					height: 50vh;
					background: #6b6b6b;
					margin-bottom: 10px;
				}
				.traffic {
					float: left;
					display: flex;
					li {
						width: 100px;
						height: 20px;
						color: #fffffe;
						background: #00df70;
						font: 12px/20px "Open Sans";
						text-align: center;
						cursor: pointer;
						border-radius: 10px;
						margin-right: 5px;
						&:hover {
							transition: 1s;
							box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
						}
					}
				}
				.branch {
					float: right;
					display: flex;
					li {
						width: 100px;
						height: 20px;
						color: #fffffe;
						background: #00df70;
						font: 12px/20px "Open Sans";
						text-align: center;
						cursor: pointer;
						border-radius: 10px;
						margin-right: 5px;

						&.on {
							color: #000;
							transition: 1s;
							box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
						}
					}
				}
			}

			.connect {
				text-align: left;
				width: 40%;
				.sns {
					margin-bottom: 40px;
					ul {
						display: flex;
						align-items: center;
						margin-right: 30px;
						li {
							margin-right: 20px;
							a {
								display: block;
								text-align: center;
								background: #00df70;
								width: 30px;
								height: 30px;
								border-radius: 50%;
								i {
									font-size: 12px;
									line-height: 30px;
									color: #fffffe;
								}
							}
						}
					}
				}
				.emailForm {
					table {
						width: 100%;
						td {
							input[type="text"] {
								height: 40px;
								width: 95%;
								border: none;
								box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
								margin-right: 10px;
								padding: 10px;
							}
							input[type="email"] {
								width: 100%;
								height: 40px;
								border: none;
								box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
								padding: 10px;
							}
							textarea {
								width: 100%;
								height: 100px;
								border: none;
								box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
								margin: 10px 0 40px;
								padding: 10px;
							}
							.btnSend {
								@include btnMore();
								border: none;
							}
						}
					}
				}
			}
		}
	}
}
